body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#background {
    position: fixed;
    height: 100%;
    width: 100%;
}

#background h1 {
    position: absolute;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    height: 160px;
    width: 100%;
    padding-top: 10px;
    background-color: #fbd118;
    text-align: center;
    font-family: 'Fredoka One', cursive;
}

#signup-form {
    position: absolute;
    width: 100%;
    height: 100%;
    margin-top: 100px;
}

#blocky_carousel {
    display: inline-block;
    margin: auto;
    width: 100%;
    height: 100%;
    overflow: hidden;
}



.blocky_panel {
    position: absolute;
    background-color: #222222;
    display: block;
    margin: 10px 10px 10px 10px;
    border-radius: 10px;
    padding: 10px;
    right: 0;
    left: 0;
    height: 300px;
}

.blocky_panel h2,p {
    color: #ffffff;
    font-family: 'Noto Sans', sans-serif;
    font-weight: 400;
}

.blocky_panel .link_center {
    text-align: center;
    margin-top: 10px;
}

.blocky_panel .link_center a {
    color: #fbd118;
    text-decoration: none;
}


.blocky_panel h3 {
    color: #fbd118;
    font-family: 'Noto Sans', sans-serif;
    font-weight: 400;
}

.blocky_panel p {
    font-size: 14px;
}

.blocky_panel input {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    height: 40px;
    border: none;
    bottom: 0px;
    margin-top: 20px;
    background-color: #F0F0F0;
    padding: 10px;
    font-size: 14px;
    font-family: 'Noto Sans', sans-serif;
    border-radius: 8px;
    font-weight: lighter;
}

.blocky_panel button {
    color: #222222;
    background-color: #fbd118;
    font-family: 'Noto Sans', sans-serif;
    font-weight: bold;
    cursor: pointer;
    height: 40px;
    border-radius: 8px;
    border: none;
    font-size: 16px;
    margin-top: 20px;
    width: 100%;
}

.blocky_panel button:disabled {
    background-color: #A0A0A0;
    cursor: not-allowed;
}

.blocky_panel[hidden] {
    animation: slide-out 1s;
    animation-fill-mode: forwards;
}

@keyframes slide-out {
    from {
        top: 0;
        opacity: 1;
    } 
    to {
        top: 100px;
        opacity: 0;
    }
}


.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

#navbar {
    position: fixed;
    top: 0;
    width: 100%;
}

#navbar h1 {
    margin-top: 0px;
    padding-top: 25px;
    text-align: center;
    font-weight: lighter;
    font-size: 25px;
    font-family: 'Noto Sans';
}

#navbar #bg {
    display: inline-flex;
    width: 100%;
    height: 120px;
    box-shadow: rgba(51, 51, 51, 0.159) 0px 0px 5px;
}

.container {
    display: inline-block;
    cursor: pointer;
    margin-top: 40px;
    margin-left: 20px;
    margin-right: 20px;
}
  
.bar1, .bar2, .bar3 {
    width: 40px;
    height: 6px;
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
    transform: rotate(45deg) translate(-8px, -8px);
}

#sidebar {
    position: fixed;
    padding-top: 70px;
    height: 100%;
    display: block;
    padding-left: 20px;
    padding-right: 20px;
    width: 75%;
    top: 0;
    margin-top: 60px;
    margin-bottom: 50px;
    background-color: #F0F0F0;
    box-shadow: rgba(51, 51, 51, 0.344) 0px 0px 10px;
    overflow-y: scroll;
    transform: translateX(-100%);
    bottom: 0;
}

#sidebar[hidden=false] {
    animation: forwards slide-in 500ms;
}

#sidebar[hidden=true] {
    animation: forwards slide-out-nav 500ms;
}

@keyframes slide-out-nav {
    from {
        transform: translateX(0%);
    } to {
        transform: translate(-100%);
    }
}

@keyframes slide-in {
    from {
        transform: translateX(-100%)
    }
    to {
        transform: translate(0%);
    }
}

#sidebar button {
    border: none;
    background-color: #FFFFFF;
    width: 100%;
    box-sizing: border-box;
    height: 50px;
    font-size: 15px;
    font-family: 'Noto Sans';
    cursor: pointer;
    margin-bottom: 10px;
    margin-top: 0px;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}
  
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 5px;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
